.welcome-block{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: @2nd-color;

    img{
        position: absolute;
        right: 0px;
        width: 200px;
        height: 250px;
        margin: 40px 20px 0px 20px;
        z-index: 1;
        opacity: 0.3;

        @media (min-width: 780px){
         position: relative;
         right: 0px;
         opacity: 1;

        }


    }
}

.welcome-text{
    position: relative;
    margin: 20px 20px 40px 20px;
    z-index: 2;
    left: 0px;
    width: 700px;

    h3{
        font-size: 24px;

            @media (min-width: 4
            80px){
                font-size: 30px;
            }

            @media (min-width: 780px){
                font-size: 36px;}
    }
}

.welcome-text-secondary{
        opacity: 0;
        transition: opacity 0.25s;

            @media (min-width: 900px){
                opacity: 1;

            }
}


.appear{
    opacity: 1;
}

.text-button{
    display: block;
    border: 1px solid @1st-color;
    font-size: 14px;
    padding: 5px 10px 5px 10px;
    border-radius: 5px;
    background-color: @4th-color;
    color: @1st-color;
    margin-top: 20px;
    outline: none;

    position: absolute;
    bottom: 35%;

         @media (min-width: 580px){
            bottom: 30%;
         }

         @media (min-width: 900px){
            display: none;

        }

    &:hover{
        color: @2nd-color;
        background: @5th-color;
    }
    

    &:active{
        background: @1st-color;
        color: @2nd-color;
    }
}

.button-move{
    position: relative;
    bottom: 0px;
}